// SECTION :: START

.sidebar {
	z-index: 1000;
	position: fixed;
	top: 0;
	left: 0;
	display: flex;
	align-items: center;
	width: 200px;
	height: 100%;
	padding: 30px 0;
	box-shadow: 4px 0 15px 0 rgba(0, 0, 0, 0.1);
	background-color: #163A5F;

	&__wrapper {
		display: flex;
		width: 100%;

		ol {
			display: flex;
			flex-direction: column;
			width: 100%;

			li {
				display: flex;
				flex-direction: column;
				width: 100%;

				> a {
					position: relative;
					display: flex;
					width: 100%;
					padding: 0 20px;
					background-color: transparent;
					font-weight: 500;
					font-size: 16px;
					line-height: 50px;
					text-transform: uppercase;
					color: #fff;
					@include transition(background-color, color);

					&.is-disabled {
						pointer-events: none;
						opacity: 0.55;
					}

					&:hover,
					&.is-active {
						background-color: #ffffff;
						color: #163A5F;
						@include transition(background-color, color);
					}
				}

				> div {
					display: flex;
					flex-direction: column;
					padding-left: 20px;

					a {
						display: flex;
						padding: 0 0 0 20px;
						font-weight: 500;
						font-size: 15px;
						line-height: 40px;
						color: #fff;

						&:hover {
							text-decoration: underline;
						}

						&.is-active {
							text-decoration: underline;
						}
					}
				}
			}
		}
	}
}

// SECTION :: END
